<!-- pages/supply/create.vue -->
<template>
	<view class="create-container">
		<uni-forms ref="formRef" :model="formData" :rules="formRules">
			<!-- 基础信息 -->
			<uni-card title="基本信息" margin>
				<uni-forms-item label="供应标题" required name="title">
					<uni-easyinput v-model="formData.title" placeholder="请输入供应商品名称" maxlength="30" />
				</uni-forms-item>

				<uni-forms-item label="产品类目" required name="category">
					<multi-level-select :levels="categoryLevels" @change="handleCategoryChange" />
				</uni-forms-item>
			</uni-card>

			<!-- 多媒体信息 -->
			<uni-card title="产品图片" margin>
				<uni-file-picker v-model="formData.images" fileMediatype="image" limit="9" :image-styles="imageStyles"
					@select="handleImageSelect" />
				<view class="image-tip">
					<text>最多上传9张，支持PNG/JPG格式</text>
					<switch checked @change="setImagePrivacy" /> 仅供应商可见
				</view>
			</uni-card>

			<!-- 供应详情 -->
			<uni-card title="供应详情" margin>
				<uni-forms-item label="生效时间" required>
					<uni-datetime-picker type="daterange" v-model="formData.dateRange" />
				</uni-forms-item>

				<uni-forms-item label="阶梯价格" required>
					<view class="price-ladder" v-for="(item,index) in formData.prices" :key="index">
						<uni-easyinput type="digit" v-model="item.min" placeholder="起订量" :disabled="index > 0" />
						<text class="unit">-</text>
						<uni-easyinput type="digit" v-model="item.max" placeholder="最大量"
							:disabled="index < formData.prices.length-1" />
						<uni-easyinput type="digit" v-model="item.price" placeholder="单价" prefix-icon="rmb" />
						<uni-icons v-if="formData.prices.length > 1" type="close" class="remove-btn"
							@click="removePrice(index)" />
					</view>
					<button class="add-btn" @click="addPrice">+ 新增价格段</button>
				</uni-forms-item>
			</uni-card>

			<!-- 提交按钮 -->
			<view class="submit-bar">
				<button type="primary" class="submit-btn" @click="handleSubmit" :disabled="submitting">
					{{ submitting ? '提交中...' : '立即发布' }}
				</button>
			</view>
		</uni-forms>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				formData: {
					title: '',
					category: [],
					materialCode: '',
					images: [],
					dateRange: [],
					prices: [{
						min: 1,
						max: null,
						price: null
					}],
					imagePrivate: false
				},
				categoryLevels: [],
				codeExists: false,
				validating: false,
				submitting: false,
				imageStyles: {
					width: 200,
					height: 200,
					border: {
						color: '#eee',
						width: 1,
						style: 'solid'
					}
				},
				formRules: {
					title: {
						rules: [{
								required: true,
								errorMessage: '标题不能为空'
							},
							{
								minLength: 5,
								maxLength: 30,
								errorMessage: '长度5-30个字符'
							}
						]
					},
					category: {
						rules: [{
							validate: (val) => val.length === 3,
							errorMessage: '请选择三级分类'
						}]
					},
					materialCode: {
						rules: [{
								required: true,
								errorMessage: '编码不能为空'
							},
							{
								pattern: /^[A-Z0-9]{8}$/,
								errorMessage: '8位大写字母+数字组合'
							}
						]
					}
				}
			}
		},

		async created() {
			await this.loadCategories()
		},

		methods: {
			async loadCategories() {
				try {
					const res = await this.$api.getSupplyCategories()
					this.categoryLevels = res.data
				} catch (error) {
					uni.showToast({
						title: '类目加载失败',
						icon: 'none'
					})
				}
			},

			handleCategoryChange(selectedIds) {
				this.formData.category = selectedIds
			},


			async handleImageSelect(files) {
				try {
					const uploadTasks = files.tempFilePaths.map(file =>
						this.$api.uploadImage(file, 'supply')
					)
					this.formData.images = await Promise.all(uploadTasks)
				} catch (error) {
					uni.showToast({
						title: '图片上传失败',
						icon: 'none'
					})
				}
			},

			setImagePrivacy(e) {
				this.formData.imagePrivate = e.detail.value
			},

			addPrice() {
				const lastItem = this.formData.prices[this.formData.prices.length - 1]
				this.formData.prices.push({
					min: lastItem.max ? lastItem.max + 1 : 1,
					max: null,
					price: null
				})
			},

			removePrice(index) {
				if (this.formData.prices.length <= 1) return
				this.formData.prices.splice(index, 1)
			},

			async handleSubmit() {
				try {
					await this.$refs.formRef.validate()

					if (this.codeExists) {
						return uni.showToast({
							title: '物料编码已存在',
							icon: 'none'
						})
					}

					this.submitting = true
					await this.$api.createSupply({
						...this.formData,
						startDate: this.formData.dateRange[0],
						endDate: this.formData.dateRange[1],
						priceLadder: this.formData.prices
					})

					uni.showToast({
						title: '提交成功'
					})
					setTimeout(() => uni.navigateBack(), 1500)
				} catch (error) {
					if (error.errMsg !== 'request:fail abort') {
						uni.showToast({
							title: error.message || '提交失败',
							icon: 'none'
						})
					}
				} finally {
					this.submitting = false
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.create-container {
		padding: 24rpx 32rpx 120rpx;
		background: #f5f5f5;

		.validate-tip {
			height: 40rpx;
			font-size: 24rpx;
			margin-top: 8rpx;

			.validating {
				color: #666;
			}

			.error {
				color: #dd524d;
			}
		}

		.image-tip {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-top: 20rpx;
			padding: 0 16rpx;
			color: #666;
			font-size: 24rpx;

			switch {
				transform: scale(0.8);
			}
		}

		.price-ladder {
			display: flex;
			align-items: center;
			gap: 16rpx;
			margin-bottom: 24rpx;

			::v-deep .uni-easyinput {
				flex: 1;

				&__content-input {
					text-align: center;
					padding: 16rpx;
				}
			}

			.unit {
				color: #666;
				width: 40rpx;
				text-align: center;
			}

			.remove-btn {
				color: #666;
				padding: 8rpx;
			}
		}

		.add-btn {
			width: 100%;
			height: 80rpx;
			line-height: 80rpx;
			background: #f0f0f0;
			color: #666;
			border-radius: 8rpx;
			font-size: 28rpx;
		}

		.submit-bar {
			position: fixed;
			bottom: 0;
			left: 0;
			right: 0;
			padding: 24rpx 32rpx;
			background: #fff;
			box-shadow: 0 -4rpx 16rpx rgba(0, 0, 0, 0.06);

			.submit-btn {
				width: 100%;
				height: 88rpx;
				line-height: 88rpx;
				border-radius: 44rpx;
				font-size: 32rpx;
				background: #007AFF;
				color: #fff;

				&[disabled] {
					opacity: 0.6;
				}
			}
		}
	}
</style>